<script setup>
import { defineProps, defineEmits, ref } from 'vue'
import { useStore } from 'vuex'
import { generateNewStyle, writeNewStyle } from '@/utils/theme'

defineProps({
  modelValue: {
    type: Boolean,
    required: true
  }
})

const store = useStore()
const emits = defineEmits(['update:modelValue'])

const themeColor = ref(store.getters.mainColor)
const predefineColor = [
  '#ff4500',
  '#ffd700',
  '#90ee90',
  '#00ced1',
  '#1e90ff',
  '#c71585',
  'rgba(255,69,0,0.68)',
  'rgb(255,120,0)',
  'hsv(51,100,98)',
  'hsva(120,40,94,0.5)',
  'hsl(181,100%,37%)',
  'hsla(209,100%,56%,0.73)',
  '#c7158577'
]

const closed = () => {
  emits('update:modelValue', false)
}

const confirm = async () => {
  const newStyle = await generateNewStyle(themeColor.value)
  writeNewStyle(newStyle)
  store.commit('theme/setMainColor', themeColor.value)
  closed()
}
</script>

<template>
  <el-dialog
    :title="$t('msg.universal.title')"
    :model-value="modelValue"
    @close="closed"
    width="22%"
  >
    <!-- content -->
    <div class="content">
      <p class="title">{{ $t('msg.theme.themeColorChange') }}</p>
      <el-color-picker
        v-model="themeColor"
        :predefine="predefineColor"
      ></el-color-picker>
    </div>
    <!-- footer -->
    <template #footer>
      <el-button @click="closed">{{ $t('msg.universal.cancel') }}</el-button>
      <el-button type="primary" @click="confirm">{{
        $t('msg.universal.confirm')
      }}</el-button>
    </template>
  </el-dialog>
</template>

<style lang="scss" scoped>
.content {
  text-align: center;
  .title {
    margin-bottom: 20px;
  }
}
</style>
